import {Link} from "react-router-dom";
import { useState } from "react";
import {animate, motion} from "framer-motion"
import NavButton from "./elements/NavButton";
import NavCloseButton from "./elements/NavCloseButton";
import NavOpenButton from "./elements/NavOpenButton";


function Navbar(props){

    const animationFinal = {scale: [null, 1.1, 1.05], duration: 10};
    const animationInitial = {scale: 1};
    const animationTransition= {duration: 0.3}

    const [sideBarOpen, setSideBarOpen] = useState(false)

    const showSidebar = () => {
        const responsiveNav = document.getElementById("responsive-nav");
        const openButton = document.getElementById("nav-open-button")
        const dur = 0.3
        const screenWidth = window.innerWidth;
        if (!sideBarOpen){
            openButton.disabled = true;
            responsiveNav.style.display = "flex"
            if (screenWidth <= 500) {
                animate(responsiveNav, { opacity: [0, 1] }, {duration: dur})
                responsiveNav.style.boxShadow = "none"
            } else {
                animate(responsiveNav, { x: [260, 0] }, { type: "spring", duration: dur })
                responsiveNav.style.boxShadow = "10px 0 10px #00000033"
            }
            setTimeout(() => {
                setSideBarOpen(true);
                openButton.disabled = false;
            }, dur * 1000)
        }

        setSideBarOpen(true);
    };

    const hideSidebar = () => {
        const responsiveNav = document.getElementById("responsive-nav");
        const closeButton = document.getElementById("nav-close-button");
        const dur = 0.3
        const screenWidth = window.innerWidth;
        const screenHeight = window.innerHeight;
        if (sideBarOpen) {
            closeButton.disabled = true;
            if (screenWidth <= 500) {
                animate(responsiveNav, {y: [0, screenHeight]}, {type: "spring", duration: dur})
                responsiveNav.style.boxShadow = "none"

            } else {
                animate(responsiveNav, {x: [0, 260]}, {type: "spring", duration: dur})
                responsiveNav.style.boxShadow = "none"
            }
            setTimeout(() => {
                responsiveNav.style.display = "none";
                setSideBarOpen(false);
                animate(responsiveNav, {x: 0, y: 0}, {duration: 0})
                closeButton.disabled = false;
            }, dur * 1000)
        }
    };


    return (
            <header className={"navbar-shadow"}>
                <nav>
                    <motion.ul animate={"showDiv"} initial={"hideDiv"} className={"responsive-nav"} id={"responsive-nav"}>
                        <li onClick={hideSidebar} id={"nav-close-button"} >
                            <a href={"#"} title={"Close Menu"} >
                                <NavCloseButton/>
                            </a>
                        </li>
                        <li>
                            <Link to={"/"} onClick={hideSidebar}>
                                <motion.p
                                    className={"growing-hover"}
                                    initial={animationInitial}
                                    whileHover={animationFinal}
                                    transition={animationTransition}
                                >
                                    Home
                                </motion.p>
                            </Link>
                        </li>
                        <li>
                            <Link to={"/projects"} onClick={hideSidebar}>
                                <motion.p
                                    className={"growing-hover"}
                                    initial={animationInitial}
                                    whileHover={animationFinal}
                                    transition={animationTransition}
                                >
                                    Projects
                                </motion.p>
                            </Link>
                        </li>
                        <li>
                            <Link to={"/experience"} onClick={hideSidebar}>
                                <motion.p
                                    className={"growing-hover"}
                                    initial={animationInitial}
                                    whileHover={animationFinal}
                                    transition={animationTransition}
                                >
                                    Experience
                                </motion.p>
                            </Link>
                        </li>
                    </motion.ul>
                    <ul>
                        <li>
                            <Link to={"/"}>
                                <h1
                                    className={"growing-hover"}
                                    initial={animationInitial}
                                    whileHover={animationFinal}
                                    transition={animationTransition}
                                >
                                    Caelums Portfolio
                                </h1>
                            </Link>
                        </li>
                        <li className={"mobile-hide"}>
                            <Link to={"/"}>
                                <motion.p
                                    className={"growing-hover"}
                                    initial={animationInitial}
                                    whileHover={animationFinal}
                                    transition={animationTransition}
                                    whileTap={animationFinal}
                                >
                                    <NavButton buttonText={"Home"}/>
                                </motion.p>
                            </Link>
                        </li>
                        <li className={"mobile-hide"}>
                            <Link to={"/projects"}>
                                <motion.p
                                    className={"growing-hover"}
                                    initial={animationInitial}
                                    whileHover={animationFinal}
                                    transition={animationTransition}
                                    whileTap={animationFinal}
                                >
                                    <NavButton buttonText={"Projects"}/>
                                </motion.p>
                            </Link>
                        </li>
                        <li className={"mobile-hide"}>
                            <Link to={"/experience"}>
                                <motion.p
                                    className={"growing-hover"}
                                    initial={animationInitial}
                                    whileHover={animationFinal}
                                    transition={animationTransition}
                                    whileTap={animationFinal}
                                >
                                    <NavButton buttonText={"Experience"}/>
                                </motion.p>
                            </Link>
                        </li>
                        <li onClick={showSidebar} id={"nav-open-button"} className={"hamburger"}>
                            <a href={"#"} title={"Open Menu"}>
                                <NavOpenButton/>
                            </a>
                        </li>
                    </ul>
                </nav>
            </header>
    );
};

export default Navbar