import it from "./images/experience_IT.png"
import experience2 from "./images/experience_2.png"
import ExperienceIT from "./content/experience/ExperienceIT";
import ExperienceTwo from "./content/experience/ExperienceTwo";

function Experience(){
    return (
        <div className={"projects"}>
            <div className={"row"}>
                <h1>Experience</h1>
                <hr/>
            </div>
            <div className={"row"}>
                <div className={"col-md-3"}>
                    <img src={it} width={"100%"} style={{marginBottom: "12px"}} alt={"Picture of me at the RDKB"} title={"Picture of me at the RDKB"}/>
                </div>
                <div className={"col-md-9"}>
                    <ExperienceIT/>
                </div>
            </div>
            <hr/>
            <div className={"row"}>
                <div className={"col-md-9"}>
                    <ExperienceTwo/>
                </div>
                <div className={"col-md-3"}>
                    <img src={experience2} width={"100%"} style={{marginBottom: "12px"}} alt={"Picture of the Interior Health, "}/>
                </div>
            </div>
        </div>
    );
}

export default Experience