

function Card({title, imgSource, imgAlt, bodyText}){
    return (
        <div className={"card-container"}>
            <div className={"row"}>
                <h3>
                    {title}
                </h3>
                <hr/>
            </div>
            <div className={"row"}>
                <div className={"col"}>
                    {bodyText}
                </div>
            </div>
            <hr/>
            <div className={"row"}>
                <div className={"col"}>
                    <img src={imgSource} alt={imgAlt} title={imgAlt}/>
                </div>
            </div>

        </div>
    )
}

export default Card