import Card from "./elements/Card";

import pacman from "./images/portfolio_pacman.gif"
import housing from "./images/portfolio_housing.png"
import website  from "./images/portfolio_website.png"
import ProjectWebsite from "./content/projects/ProjectWebsite";
import ProjectPacman from "./content/projects/ProjectPacman";
import ProjectHousing from "./content/projects/ProjectHousing";
function Projects(){
    return (
        <div className={"projects"}>
            <div className={"row"}>
                <h1>Projects</h1>
                <hr/>
            </div>
            <div className={"row"}>
                <div className={"col col-12 col-lg-4"}>
                    <Card
                        title={"This Website!"}
                        bodyText={<ProjectWebsite/>}
                        imgSource={website}
                        imgAlt={"Picture representing this website, assets from Nord"}
                    />
                </div>
                <div className={"col col-12 col-lg-4"}>
                    <Card
                        title={"AI Pacman"}
                        bodyText={<ProjectPacman/>}
                        imgSource={pacman}
                        imgAlt={"A short git of running the maze with a A* ghost and human player"}
                    />
                </div>
                <div className={"col col-12 col-lg-4"}>
                    <Card
                        title={"Rental Monitor"}
                        bodyText={<ProjectHousing/>}
                        imgSource={housing}
                        imgAlt={"The picture used to represent the telegram bot"}
                    />
                </div>
            </div>

        </div>
    );
}

export default Projects